.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 5vh auto;
    width: 95%;
}

.text {
    color: #a9a9a9;
    font-size: 1em;
}

@media (max-width: 1000px) {
    .container {
        margin: 0vh auto 3vh auto;
    }
}

@media (max-width: 576px) {
    .btnContainer {
        width: 100%;
        order: -1;
        justify-content: center;
        margin: 0 auto;
        padding: 2vh 0;
    }
}